<template>
   <div class="flex flex-col gap-y-3">
      <small class="text">{{title}}</small>
      <div class="flex justify-center">
        <span class="cursor-pointer pointer border self-center text-white bg-orange rounded-4xl px-2 py-1 mx-2"
        @click="sub">
            <i class="fa fa-minus"></i>
        </span>

        <span class="w-2/6 border rounded-4xl text-gray text-center px-4 py-1">{{val}}</span>
        
        <span class="cursor-pointer pointer border self-center text-white bg-orange rounded-4xl px-2 py-1 mx-2"
        @click="add">
            <i class="fa fa-plus"></i>
        </span>
      </div>
   </div>
</template>
<script>
export default {
   name: 'NumberImput',
   props: {    
      title: {
         require: false,
         default: 'Title'
      },
      max: {
         require: false,
         default: 10
      },  
      min: {
         require: false,
         default: 0
      },    
      id: {
         require: false,
         default: 0
      },
      valor: 0
   },
   data() { 
      return { 
         val: this.min,
      }
   },      
   watch: {
      val(newVal) {
         this.$emit('updVal', {val: newVal, id: this.id})         
      }
   },
   
   methods: {
      add() {
         this.val = (this.val == this.max) ? this.max : this.val + 1
      },
      sub() {
         this.val = (this.val == this.min) ? 1 : this.val - 1
      }
   }
}
</script>
