<template>
    <div class="flex gap-3 items-center border rounded-xl p-4" v-if="(calcPrice>0)">        
        <!-- Remesas -->
        <div class="flex gap-3 w-3/12" v-if="(isProduct('Remesas'))">
            <h2>Cantidad: </h2>
            <input class="text-blue rounded-xl px-2 p-1 w-2/4" type="text" v-model="inputVal">
        </div>
        <h2>{{ (isProduct('Remesas')) ? 'Precio a pagar:' : 'Precio desde:' }} </h2>
        <span class="text-3xl text-orange font-bold">${{ calcPrice }}</span>
    </div>
</template>
<script>
import moment from 'moment'
export default {    
    name:'HomeRequest',
    props: {
        info: {
            required: true,
        }
    },
    data() {
        return {
            price: 0,
            inputVal: 100,
            prod: {}
        }
    },
    created() {
        this.price = this.info.handler.price
        this.prod = this.info.handler        
    },
    computed: {        
        calcPrice() {
            let price  = this.info.handler.price
            let msg = 'Price desde: '
            if(this.isProduct('Remesas')) {
                price = this.inputVal * this.price/100
                msg = 'Price a pagar: '
            }      
            if(price > 0) {
                this.$emit('updInfo', {
                    price_text: msg,
                    price,
                    date: moment().format('DD-MM-YYYY')
                })
            }
            return price
        }
    },    
    methods: {
        isProduct(prod) {
            if(this.info.product == prod) {
                return true
            }
            return false
        }
    }
}
</script>
