<template>
  <div class="bg-white">
    <section class="bg-white text-white">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <div class="bg-blue-180 pb-12">
            <h2 class="text-3xl pt-6 text-white 
            md:text-6xl
            lg:text-4xl">- Enviar solicitud de reserva -</h2>
            <span class="font-Clarissa text-5xl">{{ title }}</span>


            <div class="container border-dashed border-2 rounded border-white p-5">
              <!-- REQUEST INFO  -->
              <general-request :request="request_info" v-on:updInfo="updRequest"></general-request>              
              
              <form class="flex flex-col gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8
              md:flex-row">                
                <input 
                type="text" 
                placeholder="Nombre completo" 
                v-model="name" 
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/2">

                <input 
                type="text" 
                placeholder="Teléfono" 
                v-model="phone" 
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/5">

                <input 
                type="email" 
                placeholder="Email*" 
                v-model="email" 
                required
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/4">

                <textarea id="" cols="100" rows="5" 
                placeholder="Descripcion de su solicitud"
                v-model="message" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-8 py-6"
                ></textarea>

                <div class="w-full flex gap-3" @click="activeTerm = !activeTerm">  
                  <span class="cursor-pointer border rounded-4xl text-orange bg-darkBlue px-2 py-1"
                  :class="(activeTerm) ? 'text-orange' : 'text-darkBlue'">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="text-orange self-center" for="term">Términos y condiciones</label>
                </div>
                <div class="flex flex-col gap-3 w-full
                md:flex-row">
                  <span class="bg-red rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="bg-green-500 rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="success">La solicitud fue enviada exitosamente!!!</span>
                  <button type="button" class="uppercase bg-orange text-white text-xl rounded-4xl px-12 py-1 md:ml-auto"
                  @click="formSubmit">Enviar Solicitud</button>
                </div>
              </form>
            </div>
         </div>         
    </section>
    
  </div>
</template>
<script>
import GeneralRequest from '@/components/request/general_request'
import NumberImput from '@/components/NumberImput'
import Authorization from "@/services/user/auth.service";

export default {  
  name: "Contacto",
  props: {
    title: {
      require: false,
      default: 'Solicitud'
    },    
  },  
  components: {
    NumberImput,      
    GeneralRequest 
  },
  filters: {
    dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
    }
  },
  data() {
    return {      
      activeTerm: false,
      is_hotel: true,   
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,               
      request_info: {type: 'home', handler: {}},
      name: '',
      phone: '',
      email: '',      
      message: '',
      subject: '',
      msg_info: {}

    }
  },  
  created() {
    // iniciando los datos del pedido
    const info = localStorage.getItem('request_info')
    console.log(JSON.parse(info))
    this.request_info = (info) ? JSON.parse(info) : this.request_info
    // this.request = (info) ? JSON.parse(info) : this.request
    if(this.request_info.hasOwnProperty('product')) {
        this.subject = this.request_info.product
      }
  },  
  destroyed() {
    localStorage.removeItem('request_info')
  },
  watch: {
    dateRange(d) {
      this.request.date = d.startDate
    }
  },
  
  methods: {
    updPerson(obj){
        this.request.num_person = obj.val
    },
    async formSubmit() {      
      const body = {
        subject: this.subject,
        mail: this.email,
        message: {
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message,
          msg_info: this.msg_info
        },
      }            

      if(this.validEmail(this.email)) {        
        if(await Authorization.checkLogin('/send-request', {body, auth: 'bearer'}, 'post')) {
          localStorage.removeItem('request_info')
          this.success = true 
          setTimeout(() => {
              this.success = false
          }, 3000); 
        } else {
          this.error = true 
          setTimeout(() => {
              this.error = false
          }, 3000);          
        }
      } else {
        this.error = true 
        this.error_message = 'Debe insertar una dirección de correo válida.'
          setTimeout(() => {
              this.error_message = 'Error. No se pudo enviar la solicitud!!!'
              this.error = false
          }, 3000);          
      }
    },
    validEmail(mail) {
      if(mail) {
        if(mail.match(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,8}$/))
          return true
      }
      return false
    },
    updRequest(msg) {
      this.msg_info = msg
    }
  }
};
</script>
