<template>
    <div class="flex flex-col flex-wrap shadow-round bg-darkBlue text-xl px-10 py-4 mb-6
        md:flex-row">
        <div class="flex flex-col gap-3 items-start
        md:w-3/12">
            <span><b>Pais: </b> {{ request.country }}</span>
            <span v-if="(request.pole)"><b>Polo: </b> {{ request.pole }}</span>
            <span class="capitalize" v-if="(request.product != '')"><b>Producto:</b> {{ request.product }}</span>
        </div>
        <div class="flex flex-col gap-3 items-start py-3 px-8 border rounded-2xl my-2
        md:w-9/12 md:m-0">
            <div class="flex gap-x-3">
            <b class="capitalize" v-if="(ishotel)">{{ request.product_name }}</b> 
            <span class="text-base self-center" v-if="(ishotel)">({{ formatDate }})</span>
            </div>                                    
            <template v-if="(request.is_home_request)">
            <div class="flex flex-col w-full gap-x-3 gap-y-6
            md:flex-row">                      
                <div class="flex flex-col gap-y-1 items-start
                md:w-5/12">
                <small class="text ml-3">Fecha</small>
                <!-- <date-range-picker                    
                    ref="picker"
                    :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                    v-model="dateRange"
                    :time-picker="false"
                    :single-date-picker="true"
                    :autoApply="true"
                    :ranges="false"
                    class="dateRangePicker searcher w-full"
                >
                <template #input="picker">
                    <span class="text-white w-full text-center">
                        {{ picker.startDate | dateFormat }}
                    </span>
                </template>
                </date-range-picker> -->
                </div>
                <number-imput v-if="(request.product == 'Hoteles')" class="md:w-4/12" :title="'Cantidad de personas'" 
                :min="1" 
                :value="request.num_person" 
                :id="1" 
                @updVal="updPerson"></number-imput>
            </div>
            </template>
            <template v-if="(ishotel)">
                <article class="flex flex-col text-left w-full gap-y-2">
                    <section class="flex gap-x-4 text-base pb-2">
                        <span><b>Habitaciones:</b> {{ request.num_rooms }}</span>
                        <span><b>Noches:</b> {{ num_nights }}</span>
                        <span><b>Personas:</b> {{ request.num_person }}</span>
                    </section>
                    <section class="flex gap-x-3 text-base" v-for="(item, index) in request.rooms" :key="index">
                        <span >{{ item.type }}:</span> 
                        <span>{{ item.comb }}</span>
                    </section>
                </article>
            </template>
            <span class="self-end text-2xl font-bold ml-auto" v-if="(request.price > 0)">Precio total:<span class="text-orange"> ${{ request.price }}</span></span>
        </div>                
    </div>
</template>
<script>

import moment from 'moment'

export default {
    name: 'HotelRequest',   
    props: {
        request: {}
    },
    data() {        
        return {            
            dateRange: null,  
            num_nights: 1,
            // request: {
            //     country: 'Cuba',
            //     pole: '',
            //     date: '',
            //     product: 'hotel',
            //     product_name: 'Habana Libre',
            //     price: '1000',
            //     num_person: 4,
            //     num_rooms: 2,
            //     is_home_request: false,
            //     rooms: [
            //     {
            //         type: 'Junior Suit',
            //         comb: '1 adultos, 1 niño'
            //     },
            //     {
            //         type: 'Suit',
            //         comb: '2 adultos'
            //     }
            //     ]
            // },
            request_info: {type: 'home', handler: {}},
        }
  },  
  computed: {                
        ishotel() {
            return this.is_hotel = (this.request.type == 'hotel')
        },
        persons() {
            person = (this.request.product == 'hotel') ? this.request.num_person : person
            return person
        },
        formatDate() {
            const dates = this.request.date.split('/')
            let from, to, text_date
            if(dates.length > 1) {
                from = moment(dates[0])        
                to = moment(dates[1])
                this.num_nights = to.diff(from, 'days')
                text_date = from.format('DD/MM/YYYY') + ' - ' + to.format('DD/MM/YYYY')
            } else {
                text_date = moment(dates[0]).format('DD/MM/YYYY')
            }
            return text_date
        },
    },
}
</script>
