<template>
    <div class="flex flex-col gap-4 shadow-round bg-darkBlue text-xl px-10 py-4 mb-6">
        <div class="flex gap-3 text-xl">
            <h2 class="w-1/12 text-left">Destino: </h2>
            <span class="border-b w-2/5 text-left px-4">{{ request.destination }}</span>
        </div>
        <div class="flex gap-3 text-xl">
            <h2 class="w-1/12 text-left">Producto: </h2>
            <span class="border-b w-2/5 text-left px-4">{{ request.product }}</span>
        </div>
        <component :is="components[request.type]" :request="request" v-on:updInfo="updRequest"></component>                
    </div>    
    
</template>
<script>
import HomeRequest from '@/components/request/home_request'
import HotelRequest from '@/components/request/hotel_request'

export default {    
    name:'GeneralRequest',
    props: {
        request: {
            required: true,
        }
    },
    components:{
        HomeRequest,
        HotelRequest
    },    
    data() {
        return {
            components: {
                home: 'HomeRequest',
                homeh: 'HomehRequest',
                hotel: 'HotelRequest',
            },
        }
    },   
    methods: {
        updRequest(msg) {
            const message = {
                destination: this.request.destination,
                product: this.request.product,
                extra: msg
            } 
            this.$emit('updInfo', message)
        }
    } 
}
</script>
